export default {
	data() {
		return {
			inputMoneyValue: '',
			money: 35 + '元',
			isIncrease: true,
			userId: null
		}
	},
	onLoad(options) {
		const item = JSON.parse(decodeURIComponent(options.item))
		// console.log(item)
		this.money = item.totalMoney + '元'
		this.userId = item.userId
	},
	methods: {
		inputMoney(v) {
			this.inputMoneyValue = v.detail
		},
		select(v) {
			// console.log(v)
			if(v == 1) {
				this.isIncrease = true
			}else{
				this.isIncrease = false
			}
		},
		sureChange() {
			this.$http.post({
				url: '/v1/rest/manage/adjustmentMembershipFee',
				data: {
					clubId: uni.getStorageSync('clubId'),
					operatorId: uni.getStorageSync('userInfo').userId,
					userId: this.userId,
					algorithmType: this.isIncrease,
					totalMoney: Number(this.inputMoneyValue)
				}
			}).then(resp => {
				// console.log(resp)
				if(resp.status == 200) {
					uni.showToast({
						title: '调整成功！',
						duration: 2000,
						icon: 'none'
					});
					uni.navigateBack()
				}
			})
		}
	}
}
